import uniDrawer from "@/components/uni-drawer/uni-drawer.vue"
import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote
} = app;

export default {
	components: {
		uniDrawer
	},
	data() {
		return {
			kind: 0,
			active: -1,
			activeTab: 0,
			loading: true,
			firstId: '',
			secondId: '',
			page: 1,
			num: 10,
			firstCate: [],
			secondCate: [],
			list: [],
			config: [],
			cartware: []
		};
	},
	methods: {
		mpItem() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			let {
				firstId,
				secondId,
				page,
				num
			} = this;

			if (page != 1) {
				this.list = uni.getStorageSync('classify')
				return;
			}
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign,
					firstId: firstId,
					secondId: secondId,
					page: 1,
					num: num
				},
				obj
			);
			rs.getRequest('mpItemList', params, res => {
				let data = res.data;
				if (data.code == 200) {
					// if (!firstId) {
					// 	firstId = data.data.firstCate[0].id;
					// }
					// if (firstId) {
					// 	data.data.firstCate.map((e, index) => {
					// 		if (firstId == e.id) {
					// 			this.active = index;
					// 		}
					// 	});
					// }
					this.config = data.data;
					this.firstCate = data.data.firstCate;
					this.secondCate = data.data.secondCate;
					this.list = data.data.list;
					if (data.data.list.length < 10) {
						this.loading = false;
					} else {
						this.loading = true;
					}
				}
			});
		},
		// 切换一级分类
		changeFirst(index) {
			this.firstId = this.firstCate[index].id;
			this.secondId = "";
			this.kind = 0;
			this.mpItem();
		},
		// 切换二级分类
		changeSecond(index) {
			this.secondId = this.secondCate[index].id;
			this.kind = index;
			this.mpItem();
		},
		openCart(item) {
			this.cartware = item;
			this.$refs.cart.open();
			uni.hideTabBar()
		},
		onClose() {
			this.$refs.cart.close();
			uni.showTabBar();
		},
		// 显示键盘
		showKey() {
			this.$refs.popup.open();
			uni.hideTabBar()
		},
		showDraw() {
			uni.hideTabBar();
			this.$refs.drawer.open()
		},
		selectSort(index) {
			this.active = index;
		},
		cancelSort() {
			this.active = -1;
			this.$refs.drawer.close();
			uni.showTabBar();
		},
		deterSort() {
			if (this.active == -1) {
				uni.showToast({
					title: '请先选择分类',
					icon: 'none'

				})
				return;
			}
			this.firstId = this.firstCate[this.active].id;
			this.activeTab = this.active;
			this.secondId = "";
			this.kind = 0;
			this.mpItem();
			this.cancelSort();
		}
	},
	onShow() {
		this.mpItem();
	},
	onLoad() {
		uni.hideTabBar();
	}
};
